import React, {PureComponent, createRef} from "react";
import {parse} from "papaparse";
import PropTypes from "prop-types";

class ImportButton extends PureComponent {

  constructor(props) {
    super(props);
    this.textRef = createRef();
    this.onChange = this.onChange.bind(this);
  }

  _handleNewData(e) {
    const {getNewData} = this.props;
    e.preventDefault();
    const file = e.target.files[0];
    let name = file.name;
    let nameForData = name.slice(0, -4);
    if (file.type !== `application/vnd.ms-excel`) {
      // eslint-disable-next-line no-alert
      alert(`   Excel`);
    }
    //     
    const foo = () => {
      let data;
      return new Promise((resolve, reject) => {
        parse(file, {
          header: true,
          download: true,
          dynamicTyping: true,
          complete(results) {
            resolve(data = results.data);
          },
          error(err, file) {
            reject(err);
          }
        });

      });
    };

    const main = async () => {
      try {
        const data = await foo();
        const newDataObj = {
          name: nameForData,
          place: data
        };
        console.log(newDataObj);
        getNewData(newDataObj);
      } catch (err) {
        // eslint-disable-next-line no-console
        console.error(`Could not parse file`, err);
      }
    };
    main();
  }

  onChange() {
    console.log(this.textRef.current.value);
  }

  render() {
    return (
      <div className="description ">
        <h1>  </h1>
        <div className="forBuisnes">
          <p>      </p>
          <p className="principle">    .csv  .excell  </p>
          <img src="img/obrazec.jpg" alt="  " width="500" height="90" />
          <div>
            <ul>
              <li>  100  1000  = 10  .</li>
              <li>  1000  10000  = 5  .</li>
            </ul>
          </div>
          <div className="btn">
            <input type="file" id="images" name="images" className="ad-form__input visually-hidden"
              onChange={(e) => this._handleNewData(e)} />
            <label htmlFor="images" className="ad-form__drop-zone"> ...</label>
          </div>
          <div>
            <p className="answer"> </p>
            <ul>
              <li><p>           .
                : [., ., .83,  1]
                  [., .,]    .83      </p></li>
            </ul>
          </div>
        </div>

        <div className="forFamily">
          <p>        </p>
          <p className="principle">   :
            <span> , ,,</span>
          </p>
          <input type="text" placeholder=" ,,," name="pochtaRus" ref={this.textRef} onChange={this.onChange} />
        </div>
      </div>
    );
  }
}
ImportButton.propTypes = {
  getNewData: PropTypes.func.isRequired
};
export default ImportButton;
