//  (actions)
const ActionType = {
  NORMILISE_BASA: `NORMILISE_BASA`,
  GET_SERVER_STATUS: `GET_SERVER_STATUS`,
};


//   (state):
const initialState = {
  page: `mainPage`,
  isDataLoaded: false,
  isDataPost: false,
  placesNormilse: [],
  errorMessage: ``,
};


const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.NORMILISE_BASA:
      return Object.assign({}, state, {
        page: `answerPage`,
        placesNormilse: action.payload.name,
      });
  }
  return state;
};


const ActionActive = {
  PostOnServer: (newDataObj) => ({
    type: ActionType.NORMILISE_BASA, //   type
    payload: newDataObj //     ?
  })
};

//   
const Operation = {
  postData: (places) => (dispatch, getState, api) => {
    // TODO      -    
    // return api.post(`/russia/${places.id}`, {})
    return api.post(`/check/`, {places})
      .then((response) => {
        if (response.status === 200) {
          dispatch(Operation.loadData());
          //        
        }
      });
  },
  loadData: () => (dispatch, getState, api) => {
    return api.get(`/mistakes`)
      .then((response) => {
        console.log(response);
        dispatch(setIdDataLoaded(true, ``));
        //        
      });
  }
};
/**
 * @param {status} status bool- .
 * @param {err} err .
 * @return{isDataLoaded}  (     (   ))
 */
const setIdDataLoaded = (status, err) => {
  return {
    type: ActionType.GET_SERVER_STATUS,
    isDataPost: status,
    errorMessage: err
  };
};

export {
  dataReducer,
  setIdDataLoaded,
  ActionType,
  ActionActive,
  Operation,
};
